.ail, .ail:after, .ail:before, .ail *, .ail *:after, .ail *:before, .ail + .ail-btn {
  box-sizing: border-box;
}
.ail::selection, .ail:after::selection, .ail:before::selection, .ail *::selection, .ail *:after::selection, .ail *:before::selection, .ail + .ail-btn::selection {
  background: none;
}
.ail + .ail-btn {
  outline: 0;
  display: block;
  width: 45px;
  height: 24px;
  position: relative;
  cursor: pointer;
  user-select: none;
  margin: 0;
}
.ail + .ail-btn:after, .ail + .ail-btn:before {
  position: relative;
  display: block;
  content: "";
  width: 18px;
  height: 18px;
}
.ail + .ail-btn:after {
  left: 1px;
  top: 1px;
}
.ail + .ail-btn:before {
  display: none;
}
.ail:checked + .ail-btn:after {
  left: 53%;
}

.ail-light + .ail-btn {
  border-radius: 2em;
  padding: 2px;
  transition: all 0.4s ease;
}
.ail-light + .ail-btn:after {
  border-radius: 50%;
  background: #ffffff;
  transition: all 0.3s ease;
}
